import MyMasonry from '../../Layout/Masonry';
import Slider from './Slider';
import Ticker from './Ticker';
import SubLayout from '../../Layout/SubLayout';

const DisplayPosts = ({ posts, attributes, clientId }) => {
	const { layout, columns } = attributes;
	const { desktop = 3, tablet = 2, mobile = 1 } = columns;

	const PostsLoop = () => posts.map(post => <SubLayout key={post.id} post={post} attributes={attributes} />)

	switch (layout) {
		case 'grid':
			return <div className={`apbGridPosts columns-${desktop} columns-tablet-${tablet} columns-mobile-${mobile}`}>
				<PostsLoop />
			</div>;

		case 'grid1':
			return <div className='apbGrid1Posts'>
				<PostsLoop />
			</div>;

		case 'masonry':
			return <MyMasonry posts={posts} attributes={attributes} />;


		case 'slider':
			return <Slider posts={posts} attributes={attributes} clientId={clientId} />;

		case 'ticker':
			return <Ticker posts={posts} attributes={attributes} clientId={clientId} />;

		default:
			return null;
	}
}
export default DisplayPosts;